import React,{useState,useEffect} from 'react'
import TextField from '@material-ui/core/TextField';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormLabel from '@material-ui/core/FormLabel';
import Button from '@material-ui/core/Button';


function FaceDisplay(props){

  const FETCH_URL = `https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/persons?personId=${props.items.personId}`
  const proxyurl = "https://cors-anywhere.herokuapp.com/";
  const [detections, setDetections] = useState([])
  const [value, setValue] = useState(props.items.name)
  const [blacklist, setBlacklist] = useState(props.items.blacklisted ? 'true' : 'false')
  const [loading, setLoading] = useState(true)

  useEffect(() =>{
    if(props.items.personId != 'default'){
      setLoading(true)
      fetch(proxyurl + FETCH_URL) // https://cors-anywhere.herokuapp.com/https://example.com
      .then(response => response.json())
      .then((contents) =>{
        //console.log(contents.list)
        //recentPeople = contents.list
        setDetections(contents.detections)
        setLoading(false)
      })
     .catch(() => console.log("Can’t access " + FETCH_URL + " response. Blocked by browser?"))
   }
 },[props.items.personId])



  useEffect(()=>{
      console.log(props.items.personId)
      setValue(props.items.name)
      setBlacklist(props.items.blacklisted ? 'true' : 'false')
      //change below to personID
    },[props])
    /*
<input class='input-name' type='text' value={value} onChange={handleChange}/>
    <div>
      <input class='radio-blacklist' type="radio" name="blacklist" value={true} checked={blacklist} onChange={radioChange}/> true
      <input class='radio-blacklist' type="radio" name="blacklist" value={false} checked={!blacklist} onChange={radioChange}/> false
  </div>
  */

  const handleRadioChange = e =>{
    console.log('dfdf')
    console.log(e.target)

    setBlacklist(e.target.value)
  }

  const handleChange = e =>{
    setValue(e.target.value)
  }


  return(
    <div className='face-display-container'>
      <div className='check'>
        <div className='face-display-inner'>
          <div className='face-display-appearances'>
            <img src={props.items.img} alt={props.items.name}/>
          </div>
          <div className = 'face-display-info'>
            <form onSubmit={(e) => props.handleSubmit(e,{name: value, blacklisted: blacklist=='true' ? true : false, personId: props.items.personId})}>
              <TextField inputProps={{style: {fontSize: 30, color:'black'}}} value={value} onChange={handleChange} />
              <div className='radio-blacklist'>
                <FormLabel component="legend">Blacklist status</FormLabel>
                <RadioGroup saria-label="quiz" name="quiz" value={blacklist} onChange={handleRadioChange}>
                          <FormControlLabel value={'true'} control={<Radio />} label="Yes" />
                          <FormControlLabel value={'false'} control={<Radio />} label="No" />
                </RadioGroup>
            </div>
            <Button style={{

        backgroundColor: "rgba(173,216,230, 0.2)",
        padding: "1% 2%",
        fontSize: "12px",
        color:'black',
        fontWeight:'500',
        marginLeft:'15%'





    }}
    variant="contained" type='submit'>submit</Button>

          </form>
          </div>
    </div>
  </div>
  <h2>Recognitions</h2>
    <div className='face-container'>
      <div className='face-container-2'>
        <ul>
          <li className='fixed'>
            <div >Camera</div>
            <div >Timestamp</div>
          </li>
        {loading ? <li>loading...</li>: detections.map(obj =>
            <li key={obj.timestamp}>
              <div>{obj.cameraId}</div>
              <div><em>{obj.timestamp.slice(0,16)}</em></div>
            </li>
        )}
        </ul>
      </div>
    </div>
    </div>
  )
}

export default FaceDisplay
