import React, {useState, useEffect} from 'react'
import {Line} from 'react-chartjs-2'

function TotalVisitorsGraph(props){
  const [chartData, setChartData] = useState ({})

  const chart = ()=>{
    console.log(props.timePeriod)
    let labels = []
    switch(props.timePeriod){
      case 'day':
        labels = [0,4,8,12,16,20,24]
        break;
      case 'week':
        console.log("here")
        labels = ['mon','tues','wed','thurs','fri']
    }
    setChartData({
      labels: labels,
      datasets:[
        {
        label: '# of visitors',
        data: [32,44,32,0,53,53,53],
        backgroundColor:[
          'rgba(75,192,192,.6)'
        ],
        borderWidth: 4
      }
      ]
    })
  }

  useEffect(() =>{
    chart()
  },[])

  const alertBox = (e)=>{
    alert(chartData)
  }

  return(
    <div style={{height: '300px', width:'500px'}}>
      <Line data={chartData} options={{

        responsive: true,
        legend:{onHover: alertBox},
        title: {fontSize: 16, text: 'Total visitors', display: true},
        scales:{
          yAxes:[
            {
              ticks:{
                autoSkip: true,
                maxTicksLimit: 10,
                beginAtZero: true
              },
              gridLines:{
                display: false
              },
            }
          ],
          xAxes:[
            {
              gridLines:{
                display: false
              },
              time:{
                unit: 'month',

              }
            }
          ]
        }

      }}/>
    </div>
  )
}

//return(
//  <div className='total-visitors-container'>
//    <img src={revenueImg} alt="visitors"/>
//  </div>
//)

export default TotalVisitorsGraph
